import planets from '../../data2/planets.json';
const getPlanet = (planetId) => {
    return planets[planetId];
};
const allPlanets = [];
for (const planet of Object.values(planets)) {
    allPlanets.push(planet);
}
const getAllPlanets = () => {
    return allPlanets;
};
export { getAllPlanets, getPlanet };
